import React, { Component} from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import SidebarAdmin from './SidebarAdmin';

class HomeAdmin extends Component {
    render(){
        const { username, role } = this.props.user;

        if(username !== "" && role === "Admin" ){
            return (
                <div className="container-fluid">
                    <div className="row">
                        <SidebarAdmin />

                        <div className="col-md-10 bg-light pl-3 pt-3">
                                <div className="row text-left">
                                    <div className="col-md-3">
                                        <div className="card pt-1 pl-2">
                                       <p>Total Agent</p>
                                        <div className="pt-2 pb-2">
                                        
                                        <span className="float-right pr-4"><svg class="feather feather-users sc-dnqmqq jxshSx" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><path d="M17 21v-2a4 4 0 0 0-4-4H5a4 4 0 0 0-4 4v2"></path><circle cx="9" cy="7" r="4"></circle><path d="M23 21v-2a4 4 0 0 0-3-3.87"></path><path d="M16 3.13a4 4 0 0 1 0 7.75"></path></svg> 8</span>
                                        </div>
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                    <div className="card pt-1 pl-2">
                                    <p>Total Order</p>
                                    <div className="pt-2 pb-2">
                                        
                                        <span className="float-right pr-4">
                                        <svg class="feather feather-bar-chart-2 sc-dnqmqq jxshSx" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><line x1="18" y1="20" x2="18" y2="10"></line><line x1="12" y1="20" x2="12" y2="4"></line><line x1="6" y1="20" x2="6" y2="14"></line></svg> 8</span>
                                        </div>
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                    <div className="card pt-1 pl-2">
                                    <p>History Order</p>
                                    <div className="pt-2 pb-2">
                                        
                                        <span className="float-right pr-4">
                                        <svg class="feather feather-archive sc-dnqmqq jxshSx" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><polyline points="21 8 21 21 3 21 3 8"></polyline><rect x="1" y="3" width="22" height="5"></rect><line x1="10" y1="12" x2="14" y2="12"></line></svg> 8</span>
                                        </div>
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                    <div className="card pt-1 pl-2">
                                    <p>New Order</p>
                                    <div className="pt-2 pb-2">
                                        
                                        <span className="float-right pr-4"><svg class="feather feather-alert-triangle sc-dnqmqq jxshSx" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" aria-hidden="true"><path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path><line x1="12" y1="9" x2="12" y2="13"></line><line x1="12" y1="17" x2="12" y2="17"></line></svg> 8</span>
                                        </div>
                                        </div>
                                    </div>
                                </div> 
                                <div className="card mt-2 text-left">
                                <p className=" pl-2">monthly</p>
                                </div>                       
                        </div>
                    </div>

                </div>
            );
        }
        return(
            <Redirect to="/" />
        );
    }
}

const mapStateToProps = (state) => {
    return { 
        user: state.auth
    };
}

export default connect(mapStateToProps)(HomeAdmin);